.dots-container:not(.step-nav) {
  display: none !important;
}

@media screen and (min-width: 1024px) {
  .button.white-btn {
    background-image: linear-gradient(to bottom, currentColor 25%, transparent 25%);
    background-repeat: no-repeat;
    background-size: 0% 0.6em;
    background-position-x: right;
    background-position-y: bottom;
    transition: background-size 0.3s;
    padding-bottom: 0.6em;
  }

  .button.white-btn:hover:hover {
    background-position-x: left;
    background-size: 100% 0.6em;
  }

  .dots-container {
    display: flex;
    align-items: center;
    position: absolute;
    left: calc(80px + 15vw);
    top: 60px;
  }

  ._w-slider-dot {
    margin-right: 6vw !important;
  }

  ._w-slider-dot:not(:last-child)::after {
    width: calc(6vw - 10px) !important;
  }
}

.choosen-img-container {
  transition: opacity .6s;
}

.single-port.active .choosen-img-container img,
.single-port:hover .choosen-img-container img {
  opacity: 1;
}

input[type="range"] {
  position: absolute;
  top: 50%;
  left: 65%;
  width: 50vh;
  transform: translate(-50%, -50%) rotateZ(90deg) rotateY(180deg);
  height: 47px;
  outline: none;
  -webkit-appearance: none;
}

input[type="range"]::before {
  content: "LOW";
  color: #000;
  font-size: 16px;
  transform: rotateZ(90deg) rotateY(180deg) translate(50%, 50%);
  position: absolute;
  bottom: -5px;
  left: -25px;
}

input[type="range"]::after {
  content: "HIGH";
  color: #000;
  font-size: 16px;
  transform: rotateZ(90deg) rotateY(180deg) translate(50%, 50%);
  position: absolute;
  bottom: -6px;
  right: -50px;
}

input[type="range"]::-webkit-slider-thumb {
  border: 3px solid #000000;
  height: 40px;
  width: 40px;
  border-radius: 50%;
  background: #00BAFF;
  cursor: pointer;
  -webkit-appearance: none;
  margin-top: -19px;
}

input[type="range"]::-moz-range-thumb {
  border: 3px solid #000000;
  height: 40px;
  width: 40px;
  border-radius: 50%;
  background: #00BAFF;
  cursor: pointer;
}

input[type="range"]::-ms-thumb {
  margin-top: 1px;
  border: 3px solid #000000;
  height: 40px;
  width: 40px;
  border-radius: 50%;
  background: #00BAFF;
  cursor: pointer;
}

input[type="range"]::-webkit-slider-runnable-track {
  width: 100%;
  height: 3px;
  cursor: pointer;
  background: #000000;
}

input[type="range"]::-moz-range-track {
  width: 100%;
  height: 3px;
  cursor: pointer;
  background: #000000;
}

input[type="range"]::-ms-track {
  width: 100%;
  height: 3px;
  cursor: pointer;
  background: transparent;
  border-color: transparent;
  color: transparent;
}

input[type="range"]::-ms-fill-lower,
input[type="range"]::-ms-fill-upper,
input[type="range"]:focus::-webkit-slider-runnable-track,
input[type="range"]:focus::-ms-fill-lower,
input[type="range"]:focus::-ms-fill-upper {
  background: #000000;
}

@media screen and (min-width: 1024px) {
  .slide-container.choose-room {
