._w-slider-dot {
  background: #00FFB0;
  width: 13px;
  height: 13px;
  margin-right: 10vw;
  border: 1px solid #000;
  pointer-events: none;
  border-radius: 50%;
  position: relative;
}

._w-slider-dot:last-child {
  margin-right: 0;
  display: none;
}

._w-slider-dot:not(:last-child)::after {
  content: "-------------------------------------------------------------------------------";
  position: absolute;
  left: calc(100% + 5px);
  top: 50%;
  white-space: nowrap;
  transform: translateY(-50%);
  width: calc(10vw - 10px);
  overflow: hidden;
}

._w-slider-dot:nth-last-child(2) {
  margin-right: 0;
}

._w-slider-dot:nth-last-child(2)::after {
  content: none;
}

._w-slider-dot::before {
  content: attr(data-name);
  font-size: 12px;
  text-transform: uppercase;
  position: absolute;
  left: 50%;
  top: calc(100% + 5px);
  transform: translateX(-50%);
}

._w-slider-dot._w-active {
  background: #00BAFF;
  border: none;
}

._w-slider-dot._w-active ~ ._w-slider-dot {
  background: #E3E3E3;
  border: none;
}

._w-slider-dot._w-active ~ ._w-slider-dot::before {
  opacity: .3;
}

.single-port.active .choosen-img {
  opacity: 1;
}

li.prop-item:not(:first-child)::before {
  content: "";
  position: absolute;
  left: -13px;
  top: 50%;
  transform: translateY(-50%);
  width: 6px;
  height: 6px;
  background: #00BAFF;
  border-radius: 50%;
  transition: background .6s;
}

.button-container {
