  transition: opacity 450ms ease-in-out, visibility 450ms ease-in-out;

  &.is-visible {
    opacity: 1;
    visibility: visible;

    &--block {
      display: flex;
    }
  }

  &--slow {
    transition: opacity .5s ease-in-out, visibility .5s ease-in-out;
  }
}

.is-events-none {
  pointer-events: none;
}

